import React, {useState, useEffect, Fragment} from 'react';
import goldLogoSrc from './logo-no-text.png';
import lightLogoSrc from './logo30_withLight.png';
import s from './home.module.css';
import cs from "../common.module.css";

export default () => {
    const [endAnimation, setEndAnimation] = useState(false);
    const [areImagesLoaded, setAreImagesLoaded] = useState(false);
    const cacheImages = async (srcArray) => {
        const promises = await srcArray.map((src) => {
            return new Promise(function (resolve, reject) {
                const img = new Image();
                img.src = src;
                img.onload = resolve;
                img.onerror = reject;
            });
        });
        await Promise.all(promises);
        setAreImagesLoaded(true);
    }

    useEffect(() => {
        cacheImages([goldLogoSrc, lightLogoSrc]);
    });
    return (
        <Fragment>
            <section id="home" className={s.home} onClick={() => {
                setEndAnimation(true)
            }}>
                <section className={s.animationSection}>
                    <div className={areImagesLoaded ? `${s.preLoadText} ${s.preLoadTextHidden}` : s.preLoadText}>Know
                        Thyself
                    </div>
                    {areImagesLoaded && <div>
                        {!endAnimation && <div>
                            <div className={s.logoWrapper}>
                                <img className={`${s.logoAlign} ${s.logo}`}
                                     alt="Soulgineering"
                                     src={goldLogoSrc}/>
                                <img className={`${s.logoAlign} ${s.lightLogo}`}
                                     alt="Soulgineering"
                                     src={lightLogoSrc}/>
                            </div>
                            <div className={`${s.logoTextStyles} ${s.logoTextAnimation}`}>Soulgineering</div>
                        </div>}
                        {endAnimation && <div className={s.staticLogoWrapper}>
                            <img className={s.staticLogo}
                                 alt="Soulgineering"
                                 src={goldLogoSrc}/>
                            <div className={s.logoTextStyles}>Soulgineering</div>
                        </div>}
                    </div>}
                </section>
            </section>
            <section className={s.linkSection}>
                <a target="_blank" className={cs.buttonLink} href="https://forms.gle/hz44CMke7feXi7Jv7">Schedule a Session</a>
            </section>
        </Fragment>
    );
}